import {NgModule} from '@angular/core';
import {PreloadAllModules, RouterModule, Routes} from '@angular/router';

const routes: Routes = [
    {path: 'home', loadChildren: './tabs/tabs.module#TabsPageModule'},
    {path: 'article/:imageSrc', loadChildren: './article/article.module#ArticleModule'},
    {path: 'daily', loadChildren: './daily/daily.module#DailyModule'},
    {path: 'daily-history', loadChildren: './daily-history/daily-history.module#DailyHistoryModule'},
    {
        path: '',
        redirectTo: '/daily',
        pathMatch: 'full'
    }
];

@NgModule({
    imports: [
        RouterModule.forRoot(routes, {preloadingStrategy: PreloadAllModules})
    ],
    exports: [RouterModule]
})
export class AppRoutingModule {
}
